.menu {
  background-color:#E8EFF9;
  
  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 24px;
    margin-bottom: 40px;

    // @media (min-width: 744px) {
    //   height: ;
    // }
  }

  &__content {
    box-sizing: border-box;
    padding: 40px 0;
    height: 100vh;
  }

  &__list {
    list-style: none;
    padding: 0;
    margin: 0 0 40px;
  }

  &__item {
    margin-bottom: 24px;

    &:last-child {
      margin-bottom: 0;
    }
  }


  &__link {
    display: block;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;
    text-transform: uppercase;
    text-decoration: none;
    margin-bottom: 8px;
    border-bottom: 1px solid transparent;
    width: max-content;
    transition: border-color 0.3ms;
    font-weight: 700;
    font-size: 22px;
    line-height: 22px;
    text-transform: uppercase;

    color: #292929;

    &:hover{
      border-color: #292929;
    }
  }

  &__call {
    display: block;
    font-style: normal;
    font-weight: 700;
    text-decoration: none;
    text-transform: uppercase;
    color: #292929;

    &--phone {
      font-size: 16px;
      line-height: 27px;
      margin-bottom: 16px;
    }

    &--action {
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      padding-bottom: 8px;
      width: max-content;
      border-bottom:1px solid #292929;
    }
  }
}