.icon {
  position: relative;
  display: block;
  // width: 100%;
  // padding-bottom: 100%;

  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  text-decoration: none;
  color:#292929;

  @media (min-width: 744px) {
    width: 24px;
    height: 24px;
  }

//  @include onDesktop() {
//   height: 24px;
//   width: 24px;
//  }


  &--phone {
    display: block;
    background-image: url('../images/Icon-Phone-call.svg');
    width: 24px;
    height: 24px;
    color: #292929;

    
  }

  &--cross {
    background-image: url('../images/Icon-Close.svg');
    width: 24px;
    height: 24px;
    color: #292929;
  }

  &--menu {
    background-image: url('../images/Icon-Burger-menu.svg');
    width: 24px;
    height: 24px;
    margin-left: 30px;

    @media (min-width: 744px) {
      width: 32px;
      height: 32px;
      margin-left: 40px;
    }

    &:hover {
      background-image: url('../images/Icon-Burger-menu-hover.png');
      width: 24px;
      height: 24px;

      @media (min-width: 744px) {
      width: 32px;
      height: 32px;
      }
    }
  } 
    &__tooltips {
      position: absolute;
      font-style: normal;
      font-weight: 700;
      top: 50%;
      transform: translate(-100%, -50%);
      width: max-content;
      padding-right: 12px;
      font-size: 12px;
      line-height: 16px;
      letter-spacing: 2px;
      text-transform: uppercase;
      pointer-events: none;
      opacity: 0;
      color: #292929;
    

    &:hover  &__tooltips {
    opacity: 1;
    pointer-events: all;
    text-decoration: none;
    }
  }

