.header {
  background-image: url("../images/header_background_mobile.png");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  // width: 786px;
  height: 100vh;

  @media (min-width: 744px) {
    background-image: url("../images/header_background_tablets.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100vh;
  }
   
  @media (min-width: 1024px) {
    background-image: url('../images/header_background.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    height: 100vh;
  }

  &__content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    padding: 40px 0 94px;
    height: 100vh;

    @media (min-width: 744px) {
      padding: 52px 0 40px;
    }

    @media (min-width: 1024px) {
      padding: 52px 0 64px;
    }
  }

  &__top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #292929;
    // display: grid;
    // grid-template-columns: var(--icond-width) 1fr var(--icond-width);
    // justify-items: center;
    // align-items: center;
    // @include top-actions;
  }
  
  &__icon {
    display: flex;
    justify-content: space-between;
    // padding-right: 30px;
  }

  &__title {
    margin: 0;
    font-style: italic;
    font-weight: 800;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: -1.5px;
    color: #292929;

    @media (min-width:744px) {
      // margin: 0;
      font-style: italic;
      font-weight: 800;
      font-size: 48px;
      line-height: 48px;

      letter-spacing: -1.5px;

      color: #292929;
    }
    // @include onDesktop() {
    // font-size: 80px;
    // line-height: 80px;
    // letter-spacing: -1.5px;
    // }

    &::before {
    display: block;
    content:"";
    background-image: url('../images/Vector.svg');
    width: 32px;
    height: 27px;
    // background-color: transparent;
    border: 3px solid transparent;
    margin-bottom: 16px;

    @media (min-width: 744px) {
      background-image: url('../images/Sound_waves_tablet.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      color: #292929;
      border-radius: 2px;
      margin-bottom: 32px;
    }

    @media (min-width: 1024px) {
      background-image: url('../images/Sound_waves_tablet.svg');
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
      width: 63px;
      height: 76px;
      color: #292929;
      border-radius: 2px;
      margin-bottom: 32px;
    }
    }
 }
}